@import "../../SassCommon/mixines";
@import "../../SassCommon/variables";

.section {
    padding-top: 190px;

    .container {
        @include container(0);

        .wrapper {
            @include flexCenter(column);

            h1 {
                margin-bottom: 10px;
                text-align: center;
            }
            h2 {
                margin-bottom: 32px;
                font-size: 22px;
                line-height: 28px;
                text-align: center;
            }
            .userWrapper {
                height: 313px;
                width: 370px;
                max-width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                padding: 36px 30px;
                border-radius: 16px;
                background-color: white;
            
                .photoWrapper {
                    width: 70px;
                    margin-bottom: 13px; //correct to pixel-perfect
            
                    img {
                        width: 100%;
                        border-radius: 50%;
                    }
                }
            
                h2 {
                    margin-bottom: 4px;
                    font-size: 22px;
                    line-height: 28px;
                    text-align: center;
                    color: $textColor;
                    width: 222px;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    overflow: hidden;
                }
                .descrWrapper {
                    @include flexCenter(column);
                    position: relative;
            
                    text-align: center;
            
                    .position,
                    .email,
                    .phone
                     {
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        overflow: hidden;
                        width: 222px;
                        font-size: 16px;
                        line-height: 26px;
                    }
                    .email {
                        cursor: pointer;
                        &:hover ~ .tooltip {
                            opacity : 1;
                        }
                    }
                    .tooltip {
                        position: absolute;
                        width: 285px;
                        right: 50%;
                        top: 55px;
                        transform: translateX(50%);
                        padding: 0 16px;
                        opacity : 0;
                        word-wrap: break-word;
                        background: #232F34;
                        border-radius: 4px;
                        color: white;
                        font-size: 16px;
                        line-height: 26px;
                        text-align: left;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 2559px) {
    .section {
        padding-top: 172px;
        .container {
            @include container(60px);
            .wrapper {
                .userWrapper {
                    width: 282px;
                }
            }
        }
    }
}
@media screen and (max-width: 1023px) {
    .section {
        .container {
            @include container(32px);
            .wrapper {
                .userWrapper {
                    width: 344px;
                }
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .section {
        padding-top: 141px;
        .container {
            @include container(16px);
            .wrapper {
                .userWrapper {
                    width: 328px;
                }
                button {
                    margin-top: 8px; // fix bug at figma project
                }
            }
        }
    }
}